body {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
}

.navbars {
  display: flex;
  overflow: hidden;
  background-color: #000000;
}

.navbar a {
  float: left;
  font-size: 16px;
  color: white;
  text-align: center;
  padding: 4px 6px;
  text-decoration: none;
}

.subnav {
  float: left;
  overflow: hidden;
}

.subnav .subnavbtn {
  font-size: 16px;
  border: none;
  outline: none;
  color: white;
  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
}



.subnav-content {

  display: none;
  position: absolute;
  left: 0;
  background-color: midnightblue;
  width: 100%;
  z-index: 1;
}

.subnav-content a {
  float: left;
  color: white;
  text-decoration: none;
}

.subnav:hover .subnav-content {
  display: block;
}

#menu {
  display: none;
}

#secret_checkbox {
  position: absolute;
  left: -9999px;
}

#secret_checkbox:checked+#menu {
  display: block;
}

.er {
  display: flex;
  flex-direction: column;
